@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* select */
//select
//#{$user-content} &, .select &
//display: block
//width: 100%

.#{$user-content}, .select, .#{$user-content} .select {
  select {
    @include selectSelection;
    display: block;
    position: relative;
    overflow: hidden;
    text-overflow: '';
    cursor: pointer;
    white-space: nowrap;

    // Стили для текста полей ввода прописываем отдельно через миксин,
    // чтобы была возможность добавить медиа развилки через тот же миксин
    & {
      @include selects-font; } } }

.#{$user-content} select {
  padding-right: 0; }

.#{$user-content} select, .select {
  @include forms-space;

  &:last-child {
    margin-bottom: 0; } }

.input-wrap {
  select, .select {
    margin-bottom: 0; } }

// Select with a custom arrow
.select {
  position: relative;

  &::before, &::after {
    content: '';
    position: absolute;
    right: 22px;
    top: 50%;
    @include size(8px, 2px);
    background-color: $text-color;
    margin-top: -1px;
    z-index: 1; }

  &::before {
    transform: rotate(45deg); }

  &::after {
    transform: rotate(-45deg);
    right: 17px; }

  select {
    padding-right: 40px; } }

// States
.#{$user-content} select, .select {

  &[disabled] {
    cursor: default;
    color: $form-text-color--disabled;
    background-color: $form-bg-color--disabled;
    border-color: $form-border-color--disabled;

    @include placeholder {
      color: $form-text-color--disabled; } }

  &:not([disabled]):not(.error):not(.success):not(.filled) {
    //form hover
    &:hover {
      border-color: $form-border-color--focus; }

    //form focus
    &:focus {
      border-color: $form-border-color--focus; } }

  // has value (js)
  &.filled:not([disabled]) {
    @include form-has-value; }

  // on focus (js)
  &.focused {
    @include form-on-focus; }

  //form success
  &.success:not([disabled]) {
    @include form-success;

    //form success placeholder
    @include placeholder {
      color: $form-success-color; } }

  //form error
  &.error:not([disabled]) {
    @include form-error;

    //form error placeholder
    @include placeholder {
      color: $form-error-color; } } }

/* select end */
